<template>
  <div class="zmb_index">
    <!-- 轮播部分 -->
    <div class="zmb_index_banner">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item v-for="(image, index) in bannerImgs" :key="index">
          <img v-lazy="image" />
        </van-swipe-item>
      </van-swipe>
    </div>
    <!-- 三个分类 -->
    <div class="zmb_index_kinds">
      <div @click="routeCourse">
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <p>特色课</p>
      </div>
      <div @click="onevsone">
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <p>一对一辅导</p>
      </div>
      <div @click="lzhrili">
        <img
          src="data:image/png;base64,"
          alt=""
        />
        <p>学习日历</p>
      </div>
    </div>
    <div v-for="(item, index) in list" :key="index">
      <section v-if="item.channel_info.type == 3">
        <FTtitle :title="item.channel_info.name"></FTtitle>
        <FamousBox>
          <FamousItem :famous="item.list"></FamousItem>
        </FamousBox>
      </section>
      <section v-if="item.channel_info.type == 1">
        <FTtitle :title="item.channel_info.name"></FTtitle>
        <ExcellentBox>
          <ExcellentItem :excellent_course="item.list"></ExcellentItem>
        </ExcellentBox>
      </section>
    </div>
    <div class="box"></div>
  </div>
</template>

<script>
import FTtitle from "../components/FTtitle/FT_title";
import FamousBox from "../components/Home/famousBox";
import FamousItem from "../components/Home/famousItem";
import ExcellentBox from "../components/Home/excellentBox";
import ExcellentItem from "../components/Home/excellentItem";

import Vue from "vue";
import { Lazyload } from "vant";

Vue.use(Lazyload);
export default {
  components: {
    FTtitle,
    FamousBox,
    FamousItem,
    ExcellentBox,
    ExcellentItem,
  },
  data() {
    return {
      //轮播图片
      bannerImgs: [
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20197Cxc53hktC1569839552.jpg",
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019MGNW3BtiS91569839576.jpg",
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/2019LnKumseuhw1569839569.jpg",
        "https://msmk2019.oss-cn-shanghai.aliyuncs.com/uploads/image/20193KAjU2cB6h1569839562.jpg",
      ],
      list: [],
    };
  },
  methods: {
    onevsone() {
      this.$router.push("/onevsone");
    },
    routeCourse() {
      this.$router.push("/course");
    },
    lzhrili() {
      this.$router.push("/rili");
    },
  },
  mounted() {
    this.$APP.home().then((res) => {
      this.list = res.data.data;
      // console.log(res);
    });
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  background: #eee;
}

.zmb_index {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #eee;
}

.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  height: 8rem;
  text-align: center;
  background-color: #39a9ed;
}

.my-swipe .van-swipe-item img {
  width: 100%;
  height: 100%;
}

.zmb_index_kinds {
  width: 100%;
  height: 6rem;
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  margin-top: -2rem;
  position: relative;
}

.zmb_index_kinds div {
  width: 30%;
  height: 75%;
  background: white;
  border-radius: 20px;
  font-size: 0.6rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-content: space-around;
  text-align: center;
}

.zmb_index_kinds div img {
  width: 1.2rem;
  height: 1.2rem;
}

.zmb_index_kinds div p {
  width: 100%;
}
.box {
  width: 100%;
  height: 3rem;
}
</style>